body {
  margin: 0;
}
svg {
  outline: green solid 1px;
}
#container {
  outline: red solid 1px;
  display: flex;
  justify-content: center;
}

.node rect {
  padding: 10px;
  // fill-opacity: 0.9;
  // shape-rendering: crispEdges;
}
// path { outline: green solid 1px;}

.link {

  fill: none;
  stroke-opacity: 0.3;
}

.link:hover {
  stroke-opacity: 1;
}

.tooltip {
  position: absolute;
  background-color: var(--mo-gray-05);
  width: 20rem;
  padding: 8px;
}
text {
  font-family: sans-serif;
  font-size: 0.7rem;
}

.issue-title,
.detail-heading {
  font-size: 1rem;
  font-weight: 700;
  display: block;
}

.total-programs,
.total-awards {
  font-size: 0.8rem;
}

.total-programs {
  font-weight: 700;
}
